//==========================================================
// стили формы модального окна
//==========================================================
// обертка при отправке формы создается  занавес с  загрузкой
.form__body {
    position: relative;
    &:after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #2c2c2cbb url("../../img/loading.gif") center / 60px no-repeat;
        opacity: 0;
        visibility: hidden;
    }
    &.__sending::after {
        opacity: 1;
        visibility: visible;
    }
}
//==========================================================
.form__contacts {
    color: $color-white;
    padding: 20px;
    padding-bottom: 0;
    box-shadow: 0 50px 50px $color-black;
    border-radius: 5px;
    h2.form__title {
        text-align: right;
    }
    .form__item {
        &:nth-child(2),
        &:nth-child(3),
        &:nth-child(4),
        &:nth-child(5) {
            display: inline-block;
        }
    }
    input.form__input {
        margin-top: 20px;
        margin-right: 50px;
        padding: 5px;
        width: 250px;
        font-size: 18px;
        font-weight: 100;
        border: none;
        outline: none;
        background: transparent;
        color: $color-white;
        border-bottom: 1px solid #ffffff9a;
        &:focus {
            border-bottom: 1px solid $color-lghtgre;
        }
        &.__error {
            border-bottom: 1px solid $color-LuminescentRed;
        }
    }
    // label:nth-child(),
    // label:nth-child(7),
    // label:nth-child() {
    //     display: flex;
    //     flex-direction: column;
    //     input {
    //         display: block;
    //         width: 50%;
    //     }
    // }
    div.select {
        padding-top: 1%;
    }
    select {
        width: 40%;
        margin: 1% 0;
        padding: 10px;
        border: none;
        border-bottom: 1px solid #ffffff9a;
        outline: none;
        color: $color-white;
        background-color: $color-bgModal;
    }
    textarea {
        display: block;
        margin-left: auto;
        margin-top: 40px;
        width: 400px;
        height: 150px;
        background: transparent;
        resize: none;
        border: none;
        outline: none;
        font-size: 20px;
        font-weight: 600;
        font-style: italic;
        color: $color-white;
        &:focus {
            background-color: #ebebeb33;
            border-radius: 5px;
        }
    }
    @import "btns.scss";
}
//==========================================================================
//ADAPTIVE адаптив модального окна
//==========================================================================
@media (max-width: 767px) {
    .form__contacts {
        label:nth-child(6),
        label:nth-child(7),
        label:nth-child(8) {
            display: flex;
            flex-direction: column;
            input {
                width: 60%;
            }
        }
    }
}
//==========================================================================
@media (max-width: 576px) {
    .form__contacts {
        label:nth-child(6),
        label:nth-child(7),
        label:nth-child(8) {
            display: flex;
            flex-direction: column;
            input {
                // margin-left: 93px;
                width: 80%;
            }
        }
    }
}
//==========================================================================
@media (max-width: 320px) {
    .form__contacts {
        label:nth-child(6),
        label:nth-child(7),
        label:nth-child(8) {
            display: flex;
            flex-direction: column;
            input {
                // margin-left: 93px;
                width: 95%;
            }
        }
    }
}
